import React, { useState } from 'react';

function Converter() {
  const [input, setInput] = useState("123.8125");
  const [inputBase, setInputBase] = useState(10);
  const [outputBase, setOutputBase] = useState(2);

  const [result, setResult] = useState('');



  const convertBase = () => {
    console.clear();
    let outstring ="";
    // Base    
    //   , base 10    
    let baseType = inputBase;
    if(!checkBase(input , baseType)){
      console.log("base  : "+baseType);
      console.log("    ");
      if(checkBase(input , 10)){
        console.log("10  ,  ");
        outstring += `<div style="margin-bottom: 15px; font-size: 1.2rem;">
          <strong>${baseType}    -> 10 </strong> 
        </div><br/>`;
        baseType = 10;
      }else{
        console.log("10  , ");
        console.log("");
        return "   ";
      }
    }

    outstring += `<div style="margin-bottom: 15px; font-size: 1.2rem;">
											<strong>  ():</strong> <span style="color:#007bff;">${input}</span>
										</div>`;

    let nValue = baseToTargetBase(input, baseType, outputBase);
    outstring += `<div style="margin-bottom: 15px; font-size: 1.2rem;">
										<strong>${outputBase}  :</strong> <span style="color:#28a745;">${nValue}</span>
									</div>`;

    nValue = baseToTargetBase(nValue, outputBase, baseType);
    outstring += `<div style="margin-bottom: 15px; font-size: 1.2rem;">
										<strong> (${baseType}):</strong> <span style="color:#dc3545;">${nValue}</span>
									</div>`;

    setResult(outstring);
  };

    //   ()
    const checkBase = (value ,base) => {
       //  
      const trimmedValue = value.trim();
      switch (base) {
        case 2: // 2 (0 1   +  )
          if (/^[01]+(\.[01]+)?$/.test(trimmedValue)) 
            return true;
          break;
        case 8: // 8 (0 7   +  )
          if (/^[0-7]+(\.[0-7]+)?$/.test(trimmedValue)) 
            return true;
          break;
        case 10: // 10 (0 9   +  )
          if (/^[0-9]+(\.[0-9]+)?$/.test(trimmedValue)) 
            return true;
          break;
      
        case 16: // 16 (0 9, A F  +  )
          if (/^[0-9A-Fa-f]+(\.[0-9A-Fa-f]+)?$/.test(trimmedValue)) 
            return true;
          break;
        default:
          return false;
      }
    return false;
  };

  const baseToTargetBase = (nBaseNumber, base, targetBase) => {
		let [integerPart, fractionalPart] = nBaseNumber.split('.'); //   
		let rtn = 0;
   
		console.log("      ***     ***");
		console.log(" : "+nBaseNumber);

//-------------------------------------------------------------------------------------------------------------
// *** 1   ***
//-------------------------------------------------------------------------------------------------------------

    if(0){
			// 
			let baseValue = parseInt(integerPart, base); // Base -> 10 
			rtn = baseValue.toString(targetBase).toUpperCase();  // 10 -> targetBase 
			console.log(" () : "+rtn);
		}else{ 
			// 
			let baseValue = integerPart;
			if(base != 10) //base -> 10
				baseValue = baseToDecimal(baseValue , base);
			// 10 ->  targetBase  
			rtn = decimalToBase(baseValue , targetBase);
			console.log(" () : " + rtn);
		}
//-------------------------------------------------------------------------------------------------------------
// *** 2   ***
//-------------------------------------------------------------------------------------------------------------
		if (fractionalPart !=null && fractionalPart.length >= 0) {
			//    
			let baseValue = fractionalPart; // 
			if(base != 10)  //base -> 10 
				baseValue = baseToDecimalFraction(baseValue , base);
			//10 -> targetBase  
			rtn += "." + decimalFractionToBase(baseValue , targetBase);

			console.log(" +  : " + rtn);
		}
		console.log("----------------------------------");
		return rtn; 
  };

  const baseToDecimal = (nValue, base) => {
		const inValue = nValue;
		let rtn = 0; 

		//
		let nSum = 0;
		for (let i = 0; i < inValue.length; i++) {
				const decimalValue = parseInt(inValue[i], base); 
				//    
				const power = inValue.length - 1 - i; 
				nSum += decimalValue * Math.pow(base, power); //  
		}
		rtn = nSum ;

    return rtn;
  };

  const decimalToBase = (nValue, base) => {

		const inValue = nValue; //
		const inBase = base; // base
		let rtn = ''; 

		// 
		let baseValue = '';
		let subRtn = inValue;
		while (subRtn > 0) {
			const remainder = subRtn % inBase; //  
			baseValue = remainder.toString(inBase).toUpperCase() + baseValue; //   
			subRtn = Math.floor(subRtn / inBase); //  
		}

		rtn = baseValue; // 10 -> targetBase 
		console.log("-decimalToBase ( 10  ->"+base+" )"+rtn);
		return rtn; 
  };

  const baseToDecimalFraction = (nValue, base) => {
		const inValue = nValue;
		const inBase = base; // base
	
		let rtn = 0; 
		let subRtn = 0; // 10  
		for (let i = 0; i < inValue.length; i++) {
			const decimalDigit = parseInt(inValue[i], inBase); // n -> 10
			subRtn += decimalDigit * Math.pow(inBase, -(i + 1)); //   ;
		}
		rtn = subRtn.toString().split('.')[1];
	
		console.log("-baseToDecimalFraction  ("+inBase+" -> 10 ) => "+rtn);
		return rtn; //string :  
  };

  const decimalFractionToBase = (nValue, base) => {
		let inValue = parseFloat("0."+nValue);//
		const inBase = base; // base

    let rtnValue = ''; 
		let subRtn = ''; // 
		let baseValue = '';
		let precisionLimit = 20; //    
		 //string   
		while (inValue > 0.0 && subRtn.length < precisionLimit) {
			inValue *= inBase;
			const fractionDigit = Math.floor(inValue); 
			baseValue += fractionDigit.toString(inBase).toUpperCase(); //   
			subRtn += fractionDigit;
			inValue -= fractionDigit;
		}
		rtnValue = baseValue;
		console.log("-decimalFractionToBase ( 10   ->"+base+") => "+rtnValue);
    return rtnValue; //string :  
  };

  return (
    <div style={styles.container}>
      <h1 style={styles.title}> </h1>
      <p style={styles.description}>2, 8, 10, 16  </p>
      
      <input
        type="text"
        value={input}
        onChange={(e) => setInput(e.target.value)}
        placeholder="Enter number"
        style={styles.input}
      />
      
      <div style={styles.selectContainer}>
        <div>
          <label style={styles.label}>  :</label>
          <select
            value={inputBase}
            onChange={(e) => setInputBase(parseInt(e.target.value))}
            style={styles.select}
          >
            <option value="2">Binary (2)</option>
            <option value="8">Octal (8)</option>
            <option value="10">Decimal (10)</option>
            <option value="16">Hexadecimal (16)</option>
          </select>
        </div>
        <div>
          <label style={styles.label}> :</label>
          <select
            value={outputBase}
            onChange={(e) => setOutputBase(parseInt(e.target.value))}
            style={styles.select}
          >
            <option value="2">Binary (2)</option>
            <option value="8">Octal (8)</option>
            <option value="10">Decimal (10)</option>
            <option value="16">Hexadecimal (16)</option>
          </select>
        </div>
      </div>
      
      <button onClick={convertBase} style={styles.button}>
        
      </button>
      <div className="result" dangerouslySetInnerHTML={{ __html: result }} style={styles.resultContainer}></div>
    </div>
  );
}

const styles = {
  container: {
    maxWidth: '600px',
    margin: '10% auto',
    padding: '20px',
    backgroundColor: '#f8f9fa',
    borderRadius: '10px',
    boxShadow: '0 4px 10px rgba(0, 0, 0, 0.3)',
  },
  title: {
    textAlign: 'center',
    fontSize: '2rem',
    color: '#343a40',
    marginBottom: '10px',
  },
  description: {
    textAlign: 'center',
    color: '#6c757d',
    marginBottom: '20px',
  },
  input: {
    width: '100%',
    padding: '10px',
    margin: '10px 0',
    fontSize: '1.2rem',
    borderRadius: '5px',
    border: '1px solid #ced4da',
  },
  selectContainer: {
    display: 'flex',
    justifyContent: 'space-between',
    flexWrap: 'wrap',  // Allows wrapping
    gap: '10px',       // Space between items
    marginBottom: '20px',
  },
  label: {
    display: 'block',
    marginBottom: '5px',
    fontSize: '1rem',
    color: '#495057',
  },
  select: {
    padding: '10px',
    fontSize: '1.1rem',
    borderRadius: '5px',
    border: '1px solid #ced4da',
    backgroundColor: '#fff',
    width: '180px',
    flex: '1 1 45%',  // Ensures each select box takes up ~45% of the container width
  },
  button: {
    display: 'block',
    width: '100%',
    padding: '10px',
    fontSize: '1.2rem',
    color: '#fff',
    backgroundColor: '#007bd0',
    border: 'none',
    borderRadius: '5px',
    cursor: 'pointer',
    transition: 'background-color 0.3s ease',
  },
  buttonHover: {
    backgroundColor: '#0056b3',
  },
  resultContainer: {
    marginTop: '20px',
    padding: '15px',
    backgroundColor: '#f1f3f5',
    borderRadius: '8px',
    border: '1px solid #adb5bd',
    fontSize: '1.1rem',
  },
};

export default Converter;
