import React , { useCallback } from 'react';
import myStyle from './FlowChart.module.scss';
import {  ReactFlow,  MiniMap,  Controls,  Background,  useNodesState,  useEdgesState,  addEdge,} from '@xyflow/react';
import '@xyflow/react/dist/style.css';
import { useNavigate } from 'react-router-dom';

//2024-09-01 
//pos js   
//initialEdges   js  
function FlowChart({ initialNodes, initialEdges }) {

  const navigate = useNavigate();

  //      
  const onNodeClick = (event, node) => {
    console.log('Node clicked:', node);
    navigate(`/${node.data.path}`); //   
  };

  //      
  const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
  const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);

  //   
  const onConnect = useCallback(
    (params) => setEdges((eds) => addEdge(params, eds)),
    [setEdges],
  );

  //    (:   )
  const calculateNodePositions = (nodes) => {
    return nodes.map((node, index) => ({
      ...node,
      position: { x: index * 200, y: index * 100 }, //     
    }));
  };

  //    (   )
  const createInitialEdges = (nodes) => {
    let edges = [];
    for (let i = 0; i < nodes.length - 1; i++) {
      edges.push({
        id: `e${nodes[i].id}-${nodes[i + 1].id}`,
        source: nodes[i].id,
        target: nodes[i + 1].id,
      });
    }
    return edges;
  };

  //       
  const updatedNodes = calculateNodePositions(nodes);
  const updatedEdges = createInitialEdges(updatedNodes);

  return (
    <div className={myStyle.container}>
      <ReactFlow
        nodes={updatedNodes}
        edges={updatedEdges.length > 0 ? updatedEdges : edges}
        onNodesChange={onNodesChange}
        onEdgesChange={onEdgesChange}
        onConnect={onConnect}
        onNodeClick={onNodeClick}
      >
        <Controls />
        <MiniMap />
        <Background variant="dots" gap={12} size={1} />
      </ReactFlow>
    </div>
  );
}

export default FlowChart;
